import { Injectable } from '@angular/core';

import { Observable } from 'rxjs';
import { Vendor } from '../models/vendor';
import { environment } from 'src/environments/environment';
import { HttpHeaders, HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ContactService {

  constructor(private httpservice:HttpClient) { }

  getAllContacts(id:any): Observable<Vendor[]>{
      return this.httpservice.get<Vendor[]> (environment.APIUrl+'/contactDetails/'+id);      
    
  }
  disableById(id:any):any{
    let body = JSON.stringify(id);
    console.log(id);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }
    return this.httpservice.put<Vendor[]>(environment.APIUrl+'/disablecontact/'+id,body,options);
  }
}
