import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Vendor } from '../models/vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { SaveVendorService } from '../services/save-vendor.service';
import { AuthService } from '../auth.service';


@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.css']
})
export class AddVendorComponent implements OnInit {

  vendors:Vendor[];
  vendor=new Vendor();
  vendorid: any;

  constructor(private authService:AuthService,private router: Router,private service: SaveVendorService, private route:ActivatedRoute) { }

  ngOnInit() {
    this.getVendorById()
  }

  addVendor():void{
    this.service.insertVendor(this.vendor)
    .subscribe((response) => {
      console.log(response);
      
       this.getVendor;
      this.router.navigate(['viewVendors'])
      this.resetForm();
    },(error) => {
      console.log(error);
    });
  }
  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={
      
      vendorid: null,
      vname:'',
      vaddress: '',
      vlocation: '',
      vservice: '',
      vpincode: null,
      isActive: '',
      c_id: null,
      c_name:'',
      department: '',
      email: '',
      phone : ''           
    }                                  
  }
  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }
  backtoview(){
    this.router.navigate(['vendor']); 
  }
  logout(){
    this.authService.logout();
  }

  getVendorById(){
    this.vendorid=this.route.snapshot.paramMap.get('id')
    console.log(this.vendorid);
    this.service.getVendorById(this.vendorid)
    .subscribe((vendorbyId)=>{
      this.vendor=vendorbyId;
      console.log(vendorbyId);
    },(error)=>{
      console.log(error);
    });
  }

}
