import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Vendor } from '../models/vendor';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SaveVendorService {

  getVendorById(vid: any):Observable<Vendor>{
    return this.http.get<Vendor>
    (environment.APIUrl+'/vendors/'+vid); 
  }

  
  formData:Vendor;

  constructor(private http: HttpClient) { }
  getAllVendors():Observable<Vendor[]>{
    return this.http.get<Vendor[]>
    (environment.APIUrl+'/vendor/null');      
  }

  insertVendor(vendor:Vendor){
    let body = JSON.stringify(vendor);
    console.log(vendor);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }
    if(vendor.vendorid){
      return this.http.put(environment.APIUrl+'/updatevendor',body,options);
    }
    else{
    
      return this.http.post(environment.APIUrl +'/insertvendor', body, options);
    }

  }
}