import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VendorService } from '../services/vendor.service';
import {Vendor} from '../models/vendor';
import { AuthService } from '../auth.service';


@Component({
  selector: 'app-vendors',
  templateUrl: './vendors.component.html',
  styleUrls: ['./vendors.component.css']
})
export class VendorsComponent implements OnInit {
  vendor :Vendor[];
  v = new Vendor();
  searchText;
  searchid:any;

  constructor(private authService:AuthService,private router:Router,private service:VendorService) { }

  ngOnInit() {
    this.getVendors();
  }
  getVendors():void{
    this.service.getVendors()
    .subscribe((data)=>{
      this.vendor=data;

    });
  }
  add() {
    this.router.navigate(['addVendor']);
  };
  edit(id:any){
    this.router.navigate(['addVendor',id]);
  };
  contact(id:any) {
    this.router.navigate(['contact',id]);
  };
  backtoview(){
    this.router.navigate(['vendor']); 
  }
  logout(){
    this.authService.logout();
  }
  onSelect(selectedVendor:any){
    this.searchid=selectedVendor.vendorid;
  }
  DisableVendor() {
    console.log("inside disable" + this.searchid);
    this.service.disablevendor(this.searchid)
      .subscribe((response) => {
        console.log(response);
        this.getVendors();
      }, (error) => {
        console.log(error);
      });
  }
}
