import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Vendor } from '../models/vendor';
import { ContactService } from '../services/contact.service';
import { AuthService } from '../auth.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  contact:Vendor[]
  c=new Vendor();
  searchText;
  id:any
  cid:any;
  searchid:any;
  constructor(private authService:AuthService,private service:ContactService,private router:Router,private route:ActivatedRoute) { }

  ngOnInit() {
    this.getContacts();
  }
  getContacts():void{
    this.cid=this.route.snapshot.paramMap.get('id')
    this.service.getAllContacts(this.cid)
    .subscribe((data)=>{
      this.contact=data;

    });

  }
  logout(){
    this.authService.logout();
  }
  
  pass(search:any):void{
    this.searchid=search.contact_id;
    }
    disableContact(){
      this.service.disableById(this.searchid)
      .subscribe((data)=>{
        this.contact=data;
        this.getContacts();
      })
    }
    back(){
      this.router.navigate(['vendor'])
    }
}






